
body {
  font-family: 'Roboto', sans-serif;
  background: #001A23;
  margin: 0;
  padding: 0;
  color: #f4f4f4;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
main { flex: 1; }

header {
  background-color: #002B36;
  color: white;
  padding: 20px;
  text-align: center;
  position: relative;
}
footer {
  background-color: #002B36;
  color: #aaa;
  text-align: center;
  padding: 10px;
  font-size: 0.85em;
}

#settingsIcon {
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 1.5em;
  cursor: pointer;
}
#settingsIcon::after {
  content: 'Dev Mode';
  position: absolute;
  bottom: -35px;
  right: 0;
  background: rgba(255, 107, 53, 0.9);
  color: #fff;
  padding: 2px 6px;
  font-size: 0.65em;
  border-radius: 4px;
  display: none;
}
#settingsIcon:hover::after {
  display: block;
}

section {
  margin: 20px;
  padding: 20px;
  background: #0D2B34;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0,0,0,0.4);
  position: relative;
}
button {
  background-color: #FF6B35;
  color: white;
  border: none;
  padding: 10px;
  border-radius: 4px;
  cursor: pointer;
}
button:hover {
  background-color: #cc512a;
}
.notification {
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #c7f4d7;
  color: #003920;
  padding: 12px 20px;
  border: 1px solid #6cc38e;
  border-radius: 4px;
  display: none;
  z-index: 9999;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
  font-weight: bold;
  white-space: pre-wrap;
}
input, textarea {
  width: 100%;
  margin: 10px 0;
  padding: 12px;
  font-size: 1em;
  border-radius: 4px;
  border: 1px solid #ccc;
  box-sizing: border-box;
}
textarea { font-size: 1em; }
.form-row {
  display: flex;
  gap: 10px;
}
.form-row input {
  flex: 1;
}
#addProjectBtn {
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 1.25em;
  display: none;
}
.project {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px;
  background: linear-gradient(to right, #FF6B35, #12343b);
  margin: 10px 0;
  border-radius: 6px;
}
.project a {
  color: #fff3e0;
  text-decoration: none;
  font-weight: 500;
}
.project-actions button {
  margin-left: 8px;
  display: none;
}
.project.dev-active .project-actions button {
  display: inline-block;
}
#projectModal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  display: none;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}
#projectModalContent {
  background: #fff;
  color: #000;
  padding: 20px;
  border-radius: 8px;
  width: 300px;
}
#projectModalContent label {
  display: block;
  margin-top: 10px;
  font-weight: bold;
}
